import React, { Component } from "react";
import BootstrapTable from "react-bootstrap-table-next";
import ToolkitProvider, {
  Search,
  CSVExport,
} from "react-bootstrap-table2-toolkit";
import KadarKadarPerhiasan from "./Component/Kadar_kadar_perhiasan";
import KadarPembelian from "./Component/Kadar_pembelian";
import KadarPenjualan from "./Component/Kadar_penjualan";
import { connect } from "react-redux";
import { getKadarList } from "../actions/kadarAction";
import Swal from "sweetalert2";
import notrespond from "../Images/500.svg";
import { reset } from "redux-form";
import Skeleton from "react-loading-skeleton";
import paginationFactory from "react-bootstrap-table2-paginator";
import { berhasil, gagal } from "../Alert";
import { AxiosMasterDelete, AxiosMasterPost, AxiosMasterPut } from "../Axios";
import { hideModal, showModal } from "../actions/ModalAction";
import ModalMasterData from "./Component/ModalMasterData";
import HeaderCard from "../HeaderCard";
import TourGuide from "../TourGuide";
import { finishSend, progressSend } from "../actions/LogicAction";
import { formatGram2 } from "../helper";

const { SearchBar } = Search;
const { ExportCSVButton } = CSVExport;

const maptoState = (state) => {
  return {
    kadarList: state.provinsi.kadar,
    alert: state.provinsi.alert,
    error: state.provinsi.error,
    onSend: state.provinsi.onSend,
  };
};
function hapus(dispatch, kode, tipe) {
  Swal.fire({
    title: "Anda Yakin ?",
    text: "Anda Akan Menghapus Data Ini ?",
    icon: "warning",
    showCancelButton: true,
    confirmButtonColor: "#3085d6",
    cancelButtonColor: "#d33",
    confirmButtonText: "Yes, delete it!",
  }).then((result) => {
    if (result.isConfirmed) {
      AxiosMasterDelete(`kadar/delete/${kode}&${tipe}`)
        .then((res) =>
          berhasil("Data Berhasil Dihapus!").then(() =>
            dispatch(getKadarList(tipe))
          )
        )
        .catch((err) => gagal(`Data Gagal Dihapus!, Error : ${err}`));
    }
  });
}

class Kadar extends Component {
  constructor(props) {
    super(props);
    this.state = {
      Kadar: [],
      response: "",
      tipe: "KADAR-CETAK",
      columns: [
        {
          dataField: "kadar",
          text: "Kode",
          align: "right",
          headerAlign: "right",
          formatter: (data) => formatGram2(data),
        },
        {
          dataField: "type_kadar",
          text: "Tipe Kadar",
          formatter: (data) => {
            if (data === "KADAR-BELI") {
              return "KODE AWAL";
            } else if (data === "KADAR-JUAL") {
              return "KODE TUKAR";
            } else {
              return "CAP KODE";
            }
          },
        },
        {
          dataField: "Aksi",
          text: "Aksi",
          csvExport: false,
          formatter: (rowContent, row) => {
            return (
              <div className="text-center">
                <button
                  className="btn btn-danger mr-2"
                  data-tut="reactour_4"
                  onClick={() =>
                    hapus(this.props.dispatch, row.kadar, row.type_kadar)
                  }
                >
                  <i className="fa fa-trash mr-1"></i>
                  HAPUS
                </button>
              </div>
            );
          },
        },
      ],
    };
  }

  componentDidMount() {
    this.props.dispatch(getKadarList("KADAR-CETAK"));
  }

  changeTable = (input) => (e) => {
    this.props.dispatch(getKadarList(e.target.value));
  };

  handleSubmit_perhiasan(hasil) {
    let data = {
      kadar: hasil.kadar,
      type_kadar: "KADAR-CETAK",
    };
    this.props.dispatch(progressSend());
    AxiosMasterPost("kadar/add", data)
      .then(() => this.props.dispatch(hideModal()))
      .then((res) =>
        berhasil("Data Berhasil Ditambah!")
          .then(() => this.props.dispatch(finishSend()))

          .then(() => this.props.dispatch(getKadarList("KADAR-CETAK")))
          .then(() => this.props.dispatch(reset("kadarPerhiasan")))
      )
      .catch((err) =>
        gagal("Data Gagal Ditambah!")
          .then(() => this.props.dispatch(finishSend()))
          .then(() => this.props.dispatch(hideModal()))
      );
  }
  handleSubmit_perhiasan_edit(hasil) {
    let data = {
      kadar: hasil.kadar,
      type_kadar: "KADAR-CETAK",
    };
    this.props.dispatch(progressSend());
    AxiosMasterPut("rate", data)
      .then(() => this.props.dispatch(finishSend()))
      .then(() => this.props.dispatch(hideModal()))
      .then((res) =>
        berhasil("Data Berhasil Diubah!").then(() =>
          this.props.dispatch(getKadarList("KADAR-CETAK"))
        )
      )
      .catch((err) =>
        gagal("Data Gagal Diubah!")
          .then(() => this.props.dispatch(finishSend()))
          .then(() => this.props.dispatch(hideModal()))
      );
  }

  handleSubmit_pembelian(hasil) {
    let data = {
      kadar: hasil.kadar,
      type_kadar: "KADAR-BELI",
    };
    this.props.dispatch(progressSend());
    AxiosMasterPost("kadar/add", data)
      .then(() => this.props.dispatch(finishSend()))
      .then(() => this.props.dispatch(hideModal()))
      .then((res) =>
        berhasil("Data Berhasil Ditambah!")
          .then(() => this.props.dispatch(getKadarList("KADAR-BELI")))
          .then(() => this.props.dispatch(reset("kadarPembelian")))
      )
      .catch((err) =>
        gagal("Data Gagal Ditambah!")
          .then(() => this.props.dispatch(finishSend()))
          .then(() => this.props.dispatch(hideModal()))
      );
  }
  handleSubmit_pembelian_edit(hasil) {
    let data = {
      kadar: hasil.kadar,
      type_kadar: "KADAR-BELI",
    };
    this.props.dispatch(progressSend());
    AxiosMasterPut("rate", data)
      .then(() => this.props.dispatch(finishSend()))
      .then(() => this.props.dispatch(hideModal()))
      .then((res) =>
        berhasil("Data Berhasil Diubah!").then(() =>
          this.props.dispatch(getKadarList("KADAR-BELI"))
        )
      )
      .catch((err) =>
        gagal("Data Gagal Diubah!")
          .then(() => this.props.dispatch(finishSend()))
          .then(() => this.props.dispatch(hideModal()))
      );
  }

  handleSubmit_penjualan(hasil) {
    let data = {
      kadar: hasil.kadar,
      type_kadar: "KADAR-JUAL",
    };
    this.props.dispatch(progressSend());
    AxiosMasterPost("kadar/add", data)
      .then(() => this.props.dispatch(finishSend()))
      .then(() => this.props.dispatch(hideModal()))
      .then((res) =>
        berhasil("Data Berhasil Ditambah!")
          .then(() => this.props.dispatch(getKadarList("KADAR-JUAL")))
          .then(() => this.props.dispatch(reset("kadarPenjualan")))
      )
      .catch((err) =>
        gagal("Data Gagal Ditambah!")
          .then(() => this.props.dispatch(finishSend()))

          .then(() => this.props.dispatch(hideModal()))
      );
  }
  handleSubmit_penjualan_edit(hasil) {
    let data = {
      kadar: hasil.kadar,
      type_kadar: "KADAR-JUAL",
    };
    this.props.dispatch(progressSend());
    AxiosMasterPut("rate", data)
      .then(() => this.props.dispatch(finishSend()))
      .then(() => this.props.dispatch(hideModal()))
      .then((res) =>
        berhasil("Data Berhasil Diubah!").then(() =>
          this.props.dispatch(getKadarList("KADAR-JUAL"))
        )
      )
      .catch((err) =>
        gagal("Data Gagal Diubah!")
          .then(() => this.props.dispatch(finishSend()))

          .then(() => this.props.dispatch(hideModal()))
      );
  }
  tambahModal() {
    this.props.dispatch(showModal());
    this.setState({
      isEdit: false,
    });
  }
  editModal() {
    this.props.dispatch(showModal());
    this.setState({
      isEdit: true,
    });
  }

  render() {
    return (
      <div className="container-fluid" style={{ color: "black" }}>
        <div id="notif_alert" className={this.props.alert} role="alert">
          <button
            type="button"
            className="close"
            data-dismiss="alert"
            aria-label="Close"
          >
            <span aria-hidden="true">Ã—</span>
          </button>
          <strong>Error - </strong> Tidak Bisa Mengambil Data
        </div>
        <TourGuide
          tourConfig={[
            `Untuk Menambahkan Data Kadar, Tekan Tombol ini Dan Isi kolom yang disediakan`,
            `Ini adalah List untuk mengubah data sesuai nama list yang dipilih`,
            `Ini adalah Data Kadar yang sudah tersimpan`,
            `Ini adalah tombol untuk menghapus data pada baris yang dipilih`,
          ]}
        />
        <div className="row">
          <div className="col-12">
            <div className="card">
              <HeaderCard title="Data Kadar" />
              <div className="card-body">
                <ModalMasterData
                  content={
                    this.state.isEdit ? (
                      <div className="col-lg-12 col-md-12">
                        <ul className="nav nav-tabs nav-justified nav-bordered mb-3">
                          <li className="nav-item">
                            <a
                              href="#hutang-tab"
                              data-toggle="tab"
                              aria-expanded="false"
                              className="nav-link"
                            >
                              <i className="mdi mdi-home-variant d-lg-none d-block mr-1" />
                              <span className="d-none d-lg-block">
                                Kadar Perhiasan
                              </span>
                            </a>
                          </li>
                          <li className="nav-item">
                            <a
                              href="#stock-tab"
                              data-toggle="tab"
                              aria-expanded="true"
                              className="nav-link active"
                            >
                              <i className="mdi mdi-account-circle d-lg-none d-block mr-1" />
                              <span className="d-none d-lg-block">
                                Harga Beli
                              </span>
                            </a>
                          </li>
                          <li className="nav-item">
                            <a
                              href="#piutang-tab"
                              data-toggle="tab"
                              aria-expanded="false"
                              className="nav-link"
                            >
                              <i className="mdi mdi-settings-outline d-lg-none d-block mr-1" />
                              <span className="d-none d-lg-block">
                                Harga Jual
                              </span>
                            </a>
                          </li>
                        </ul>
                        <div className="tab-content">
                          <div className="tab-pane" id="hutang-tab">
                            <KadarKadarPerhiasan
                              onSubmit={(data) =>
                                this.handleSubmit_perhiasan_edit(data)
                              }
                              onSend={this.props.onSend}
                            />
                          </div>
                          <div className="tab-pane show active" id="stock-tab">
                            <KadarPembelian
                              onSubmit={(data) =>
                                this.handleSubmit_pembelian_edit(data)
                              }
                              onSend={this.props.onSend}
                            />
                          </div>
                          <div className="tab-pane" id="piutang-tab">
                            <KadarPenjualan
                              onSubmit={(data) =>
                                this.handleSubmit_penjualan_edit(data)
                              }
                              onSend={this.handle.onSend}
                            />
                          </div>
                        </div>
                      </div>
                    ) : (
                      <div className="col-lg-12 col-md-12">
                        <ul className="nav nav-tabs nav-justified nav-bordered mb-3">
                          <li className="nav-item">
                            <a
                              href="#hutang-tab"
                              data-toggle="tab"
                              aria-expanded="false"
                              className="nav-link"
                            >
                              <i className="mdi mdi-home-variant d-lg-none d-block mr-1" />
                              <span className="d-none d-lg-block">
                                Cap Kode
                              </span>
                            </a>
                          </li>
                          <li className="nav-item">
                            <a
                              href="#stock-tab"
                              data-toggle="tab"
                              aria-expanded="true"
                              className="nav-link active"
                            >
                              <i className="mdi mdi-account-circle d-lg-none d-block mr-1" />
                              <span className="d-none d-lg-block">
                                Kode Awal
                              </span>
                            </a>
                          </li>
                          <li className="nav-item">
                            <a
                              href="#piutang-tab"
                              data-toggle="tab"
                              aria-expanded="false"
                              className="nav-link"
                            >
                              <i className="mdi mdi-settings-outline d-lg-none d-block mr-1" />
                              <span className="d-none d-lg-block">
                                Kode Tukar
                              </span>
                            </a>
                          </li>
                        </ul>
                        <div className="tab-content">
                          <div className="tab-pane" id="hutang-tab">
                            <KadarKadarPerhiasan
                              onSubmit={(data) =>
                                this.handleSubmit_perhiasan(data)
                              }
                              onSend={this.props.onSend}
                            />
                          </div>
                          <div className="tab-pane show active" id="stock-tab">
                            <KadarPembelian
                              onSubmit={(data) =>
                                this.handleSubmit_pembelian(data)
                              }
                              onSend={this.props.onSend}
                            />
                          </div>
                          <div className="tab-pane" id="piutang-tab">
                            <KadarPenjualan
                              onSubmit={(data) =>
                                this.handleSubmit_penjualan(data)
                              }
                              onSend={this.props.onSend}
                            />
                          </div>
                        </div>
                      </div>
                    )
                  }
                  title={
                    this.state.isEdit ? "Edit Data Kadar" : "Tambah Data Kadar"
                  }
                />
                <form action="#" className="mb-5">
                  <div className="form-body">
                    <div className="col-lg-12 col-md-12">
                      <div className="text-right">
                        <button
                          name="simpan"
                          id="simpan"
                          type="button"
                          className="btn btn-info"
                          onClick={() => this.tambahModal()}
                          data-tut="reactour_1"
                        >
                          Tambah Data Kadar
                        </button>
                      </div>
                    </div>
                  </div>
                </form>
                <div data-tut="reactour_3">
                  {this.props.kadarList ? (
                    <ToolkitProvider
                      keyField="id"
                      data={this.props.kadarList}
                      columns={this.state.columns}
                      search
                    >
                      {(props) => (
                        <div className="form-group">
                          <div className="row">
                            <div
                              className=" col-md-3 col-lg-3 text-left mb-3"
                              data-tut="reactour_2"
                            >
                              <select
                                className="form-control"
                                onChange={this.changeTable()}
                              >
                                <option value="KADAR-CETAK">CAP KODE</option>
                                <option value="KADAR-BELI">KODE AWAL</option>
                                <option value="KADAR-JUAL">KODE TUKAR</option>
                              </select>
                            </div>
                            <div className=" col-md-9 col-lg-9 text-right mb-3">
                              <SearchBar
                                {...props.searchProps}
                                className="form-control"
                                placeholder="PENCARIAN"
                                style={{ margin: 0, top: "50%" }}
                              />
                            </div>
                          </div>
                          <BootstrapTable
                            {...props.baseProps}
                            striped
                            hover
                            condensed
                            bordered={true}
                            headerClasses="header-table"
                            pagination={paginationFactory()}
                          />
                          <ExportCSVButton
                            {...props.csvProps}
                            className="btn btn-primary"
                          >
                            Export CSV
                          </ExportCSVButton>
                        </div>
                      )}
                    </ToolkitProvider>
                  ) : (
                    <div>
                      {this.props.error ? (
                        <div className="text-center">
                          <img
                            src={notrespond}
                            style={{ width: "30%" }}
                            alt="NOT RESPOND"
                          />
                          <br />
                          <h4>Request API Gagal, Silahkan Refresh</h4>
                        </div>
                      ) : (
                        <Skeleton width={"100%"} height={180} />
                      )}
                    </div>
                  )}
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default connect(maptoState, null)(Kadar);
