import React, { Component } from "react";
import { connect } from "react-redux";
import { Field, reduxForm } from "redux-form";
import validationKadar from "../../Validations/validasiKadar";
import { renderField } from "../../renderField";
import {  NormalizeNumberOnly } from "../../helper";

class Kadar_pembelian extends Component {
  constructor(props) {
    super(props);
    this.state = {
      response: "",
      kode_perhiasan: "",
    };
  }

  render() {
    return (
      <form onSubmit={this.props.handleSubmit}>
        <div className="row">
          {/* Awal Field */}
          <div className="col-md-10 col-lg-10">
            <Field
              name="kadar"
              normalize={NormalizeNumberOnly}
              component={renderField}
              label="Kode Awal"
            />
          </div>
          {/* Akhir Field */}
          {/* Awal Button */}
          <div className="col-md-2 col-lg-2">
            <label htmlFor="" className="text-white">
              {"Button"}
            </label>
            <div className="text-right">
              <button
                type="submit"
                className="btn btn-info btn-block"
                disabled={this.props.onSend}
              >
                {this.props.onSend ? (
                  <span
                    className="spinner-border spinner-border-sm mr-3"
                    aria-hidden="true"
                  ></span>
                ) : null}
                Selesai
                <i class="fa fa-paper-plane ml-3" aria-hidden="true"></i>
              </button>
            </div>
          </div>
          {/* Akhir Button */}
        </div>
      </form>
    );
  }
}

Kadar_pembelian = reduxForm({
  form: "kadarPembelian",
  validate: validationKadar,
  enableReinitialize: true,
})(Kadar_pembelian);
export default connect()(Kadar_pembelian);
